body{
  background-color: #a5dabf;
  height: 100vh;
}
.toDolistApp{
  min-width: 400px;
  max-width: 41vw;
  height: 444px;
  margin: 0 auto;
  margin-top: 59px;
  background-image: linear-gradient(-48deg, #113327 23%,#22352af5 77% );
  box-shadow: 1px 1px 10px #40e19796;
  border-radius: 22px;
  display: flex;
  flex-direction: column;
  align-items: center;

}
.header {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70%;
  height: 47px;
  margin-top: 12px;
}
.input {
  padding: 6px 20px;
  width:70%;
  border: none;
  color: #71da92;
  background: transparent;
  font-size: 16px;
  font-weight: 700;
  border: 2px solid #abe6c8;
  border-radius: 10px;
  cursor: pointer;
}
.input::placeholder{
  color: #71da92;
}
.input:focus{
  outline: none;
  
}
button {
  padding: 5px 11px;
  font-size: 12px;
  font-weight: bold;
  display: flex;
  align-items: center;
  border: none;
  border-radius: 4px;
  background-image: linear-gradient(-48deg, #6dc57c 37%,#4bb1ac,#59b47ff5 43% );
  color: #132b1f;
  margin-left: 10px;
  cursor: pointer;
}
.tasks {
  margin-top: 2.2rem; 
  width:70%;
  height: 60px;
  background-image: linear-gradient(-48deg, #4fd7a4 37%,#3ad67bf5 73% );
  border-radius: 12px;
  padding: 0px 11px;
  display: flex;
  align-items: center;
  
  margin-top: 11px;
  color:#15321e;
  position: relative;

}
.tasks p{
  margin-left: 20px;
  color: #15321e;
  font-size: 17px;
  font-weight:500;
}

.closeBtn{
  position: absolute;
  right: 1rem;
  transform: 1.7s;
  cursor: pointer;
}
.closeBtn:hover{
  transform: rotate(120deg);
}

.sec-1 {
    width: 87%;
    display: flex;
    align-items: center;

}
